<template>
    <div>
        <top-nav title="积分设置"></top-nav>
        <div class="wyu-main" v-loading="loading">
            <div class="wyu-block">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p class="tit">1）这里设置商城消费赠送积分比例</p>
                    <p class="tit">2）注意积分比例是按照实际支付金额来赠送的</p>
                </div>

                <div class="wyu-title">
                    <h2>积分设置</h2>
                </div>
                <el-form ref="form" :model="form" label-width="120px" size="mini">
                    <el-form-item label="积分比例" >
                        <el-input v-model="form.integral" style="width: 200px" placeholder="请输入获得积分比例"></el-input> %
                    </el-form-item>
                    <el-form-item label="积分有效时间" >
                        <el-input v-model="form.integral_exptime" style="width: 200px" placeholder="请输入积分有效时间"></el-input> 天
                    </el-form-item>
                    <el-form-item label="5星好评返积分" >
                        <el-input v-model="form.integral_five_stars_comment" style="width: 200px" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="带图好评返积分" >
                        <el-input v-model="form.integral_whith_photo_comment" style="width: 200px" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="会员积分暴击" >
                        <el-input v-model="form.integral_vip_combo" style="width: 200px" placeholder=""></el-input> 倍
                    </el-form-item>
                    <el-form-item label="积分使用规则" >
                        <html-editor title="积分使用规则" :html="form.integral_rule"  v-model="form.integral_rule"></html-editor>
                    </el-form-item>


                    <el-form-item >
                        <el-button size="small" type="primary"  @click="setIntegral()">立即保存</el-button>
                        <el-button size="small">取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>

    import * as api from "@/api";
    import HtmlEditor from "@/components/HtmlEditor";
    import WyuUpload from "@/components/Photos";
    import TopNav from "../../../components/TopNav";

    export default {
        name: 'integralSetting',

        components: {TopNav, HtmlEditor,WyuUpload},
        data() {
            return {
                loading: false,
                form: {
                    integral: 50,
                    integral_exptime: 0,
                    integral_rule: 0,
                    integral_five_stars_comment: 0,
                    integral_whith_photo_comment: 0,
                    integral_vip_combo: 0,
                }
            }
        },

        created() {
            this.getIntegral();
        },

        methods: {
            getIntegral(){
                this.loading = true;
                api.post("/shop/admin.setting/getIntegral", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.form = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },

            setIntegral() {
                this.loading = true;
                api.post("/shop/admin.setting/setIntegral", {
                    appToken: this.$store.getters.appToken,
                    integral: this.form.integral,
                    integral_exptime: this.form.integral_exptime,
                    integral_rule: JSON.stringify(this.form.integral_rule),
                    integral_five_stars_comment: this.form.integral_five_stars_comment,
                    integral_whith_photo_comment: this.form.integral_whith_photo_comment,
                    integral_vip_combo: this.form.integral_vip_combo,
                }).then(res => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },


        }
    }
</script>
